/** @type {import("../typings/phaser")}*/
// CSTをインポートしてＣＳＴのメニューまでを指定する
import { CST } from "../CST";
import { Physics } from "phaser";

export class menuScene extends Phaser.Scene {
    stageData!: object;
    constructor() {
        super({ key: CST.SCENES.MENU });

    };
    init() {
        console.log(localStorage.getItem('stageChoice'));
        var stageData = Number(localStorage.getItem('stageChoice'));
        //引き継いだ後にインスタント化
        // ステージ１なら
        switch (stageData) {
            case 1:
                this.stageData = {
                    name: "stage1",
                    playerSummonUpperLimit: 2,
                    enemyMinionMany: 2,
                    enemyBossMany: 1,
                    witch: false,
                };
                break;
            case 2:
                this.stageData = {
                    name: "stage2",
                    playerSummonUpperLimit: 3,
                    enemyMinionMany: 4,
                    enemyBossMany: 2,
                    witch: false,
                };
                break;
            case 3:
                this.stageData = {
                    name: "stage3",
                    playerSummonUpperLimit: 4,
                    enemyMinionMany: 0,
                    enemyBossMany: 0,
                    witch: true,
                };
                break;
            default:
                this.stageData = {
                    name: "stage1",
                    playerSummonUpperLimit: 2,
                    enemyMinionMany: 2,
                    enemyBossMany: 1,
                    witch: false,
                };
                break;
        };
    }
    create() {
        var stageBtn!: Phaser.Physics.Arcade.Image;
        var gameClearBtn!: Phaser.Physics.Arcade.Image;
        if (this.stageData.name === "stage1") {
            stageBtn = this.add.image(0, 0, CST.IMAGE.FIRSTSTAGEBTN).setOrigin(0, 0).setInteractive();
        }
        else if (this.stageData.name === "stage2") {
            stageBtn = this.add.image(0, 0, CST.IMAGE.SECONDSTAGEBTN).setOrigin(0, 0).setInteractive();
        }
        else if (this.stageData.name === "stage3") {
            stageBtn = this.add.image(0, 0, CST.IMAGE.THIRDSTAGEBTN).setOrigin(0, 0).setInteractive();
        }
        // ステージシーンに飛びます
        stageBtn.on("pointerdown", () => {
            this.scene.start(CST.SCENES.PLAYSCENE, this.stageData);
        });

    };

}