// マウスが乗ったときに右下がめくれるようなアニメーション
// document.getElementById('pageLowerRightBtn').addEventListener('mouseenter', turnOverBook);
// 表紙 ロードしたときに呼び出します
window.onload = function () {
  // どこまで進んだか
  if (localStorage.gameStage === undefined) {
    localStorage.gameStage = 1;
  };
  page0Cover();
  popWIndowDIsplay('page0');
};
// 表紙
function page0Cover() {
  // ページ作成関数
  nextPageGenerate('page0Cover');
};
// 1ページ目インデックス
function page1Index() {
  nextPageGenerate('page1Index');
  document.getElementById('indexPage1').addEventListener('click', () => {
  });
  document.getElementById('indexPage2').addEventListener('click', () => {
    indexJump('page2Introduction');
  });
  document.getElementById('indexPage3').addEventListener('click', () => {
    indexJump('page3CharacterIntroduction');
  });
  document.getElementById('indexPage4').addEventListener('click', () => {
    indexJump('page4HowToPlay');
  });
  document.getElementById('indexPage5').addEventListener('click', () => {
    indexJump('page5PlayGame');
  });
  document.getElementById('indexPage6').addEventListener('click', () => {
    indexJump('page6Imprint');
  });
  popWIndowDIsplay('page1');
};
// 目次から他のページに飛ぶときにめくれるように
function indexJump(nextPage) {

  const pageList = ['page0Cover', 'page1Index', 'page2Introduction', 'page3CharacterIntroduction', 'page4HowToPlay', 'page5PlayGame', 'page6Imprint'];
  for (let i = 0; i < pageList.length; i++) {
    if (nextPage === pageList[i]) {
      document.getElementById(pageList[i]).style.visibility = 'visible';
    }
    else if (pageList[i] === 'page1Index') {
      document.getElementById(pageList[i]).style.visibility = 'visible';
    }
    else {
      document.getElementById(pageList[i]).style.visibility = 'hidden';
    }
  }

  const turningPage = document.getElementById('page1Index');
  turningPage.animate(
    {
      transform: ['rotateY(20deg)', 'rotateY(120deg)'],
      opacity: ['1', '0.5'],
    },
    {
      duration: 3000,
      fill: 'forwards',
      easing: 'ease',
    },
  ).onfinish = function () {
    if (document.getElementById('backButton') !== null) {
      document.getElementById('backButton').remove();
    };
    if (document.getElementById('indexButton') !== null) {
      document.getElementById('indexButton').remove();
    };
    // めくった後戻しておく
    turningPage.animate(
      {
        transform: ['rotateY(0deg)', 'rotateY(0deg)'],
        opacity: ['1', '1'],
      },
      {
        duration: 0,
        fill: 'forwards',
        easing: 'ease',
      },
    )
    switch (nextPage) {
      case 'page2Introduction':
        page2Introduction();
        break;
      case 'page3CharacterIntroduction':
        page3CharacterIntroduction();
        break;
      case 'page4HowToPlay':
        page4HowToPlay();
        break;
      case 'page5PlayGame':
        page5PlayGame();
        break;
      case 'page6Imprint':
        page6Imprint();
        break;
      default:
        break;
    };
  };
};


// 2ページ目序章
