import { GameObjects } from "phaser";
import { Physics } from "phaser";
import { CST } from "../CST";
export class PlayerStatus {
    hp: number;
    summonTotal: number;
    // クリック禁止エリア
    prohibitedArea: boolean;
    constructor() {
        this.hp = 32;
        this.summonTotal = 0;
        this.prohibitedArea = true;
    };
};
export class ApprenticeClass extends Phaser.Physics.Arcade.Sprite {
    btn!: Phaser.Physics.Arcade.Image;
    choice!: boolean;
    skillActivation!: boolean;
    // ボスへのダメージ
    skillDamage!: number
    constructor(scene: Phaser.Scene, x: number, y: number, texture: string) {
        super(scene, x, y, texture);
        scene.add.existing(this);
        this.play(CST.ANIMEAPPRENTICE.IDRING);
        this.btn = scene.physics.add.image(x, y, CST.IMAGE.SELECT).setInteractive().setScale(5);
        this.choice = true;
        this.skillActivation = false;
        this.skillDamage = 0.05;
    }
}

export class DefaultSummon extends Phaser.GameObjects.Container {
    skillActivation!: boolean;
    // ボスへのダメージ
    knightAttackDamage!: number;
    dameAttackDamage!: number;
    // 召喚エフェクト
    summoneffect!: Phaser.GameObjects.Sprite;
    // デイムがアタックするときにオンオフを切り替える
    btn!: Phaser.Physics.Arcade.Image;
    arrowAtack!: boolean;
    // 矢を放つときにアニメーションが切り替わるように
    arrowStance!: boolean;
    // デイムのアタックするのを一回だけ実行するように
    atackTime!: boolean;
    choice!: boolean;
    collision!: Phaser.Physics.Arcade.Image;
    attackZone!: Phaser.Physics.Arcade.Image;
    attackZoneCircle!: Phaser.GameObjects.Graphics;
    // デイムのアタックするのを一回だけ実行するように
    // ボスに攻撃中にポイントが出現したらそちらに移動するように
    leaveBoss!: boolean;
    knightAtackTime!: boolean;
    moveSpeed!: number
    swordAtack!: Phaser.Physics.Arcade.Image;
    skillEffect!: Phaser.Physics.Arcade.Sprite;
    pointMove!: boolean;
    constructor(type: string, scene: Phaser.Scene, x: number, y: number, list: GameObjects.GameObject[]) {
        super(scene, x, y, list);
        //画像の描画
        this.scene.add.existing(this);
        // パラメーターの割り振り
        // このナイトが選択されているかどうか
        this.choice = true;
        this.attackZoneCircle = scene.add.circle(0,0,70).setStrokeStyle(2, 0xFFFFFF);
        if (type === "knight") {
            this.swordAtack = scene.physics.add.image(x, y, CST.IMAGE.COLLISION).setAlpha(0.01);
            this.swordAtack.body.setCircle(20).setOffset(-15, -15);
            this.knightAtackTime = false;
            this.leaveBoss = false;
            this.knightAttackDamage = 0.03;
        }
        if (type === "dame") {
            this.atackTime = true;
            this.arrowAtack = true;
            this.dameAttackDamage = 0.6;
            this.arrowStance = false;
        }
        this.pointMove = false;
        this.moveSpeed = 50;
        this.collision = scene.physics.add.image(x, y, CST.IMAGE.COLLISION).setAlpha(0.01).setScale(3);
        this.btn = scene.physics.add.image(x, y, CST.IMAGE.SELECT).setInteractive().setScale(5);
        this.attackZone = scene.physics.add.image(x, y, CST.IMAGE.COLLISION);
        this.attackZone.body.setCircle(70).setOffset(-70, -70);
        this.type = type;
        // 召喚エフェクト
        this.summoneffect = scene.add.sprite(x, y, CST.SPRITE.SUMMONPLAYERMINION).play(CST.ANIMEFECTS.PLAYERMINIONSUMMON);
        this.summoneffect.on("animationcomplete", () => { this.summoneffect.destroy() });

        // スキル発動
        this.skillActivation = false;
        this.skillEffect = scene.physics.add.sprite(x, y, CST.SPRITE.SUMMONPLAYERMINION).play(CST.ANIMEFECTS.MINIONSKILL).setDepth(-1).setVisible(false);

    };
};


export class SummonBoss extends Phaser.Physics.Arcade.Sprite {
    // 召喚中
    summon!: boolean;
    summoneffect!: Phaser.GameObjects.Sprite;

    // HP
    hp!: number;
    enemyHpBar!: Phaser.GameObjects.Graphics;
    golem!: Phaser.Physics.Arcade.Sprite;
    // 神樹と衝突する用
    collision!: Phaser.Physics.Arcade.Image;
    // コリジョン正面
    collisionfront!: Phaser.Physics.Arcade.Image;
    // 神樹へのダメージ3
    attackDamage!: number;
    // 出現後
    constructor(scene: Phaser.Scene, x: number, y: number, texture: string, name: string) {
        super(scene, x, y, texture);
        scene.physics.world.enable(this);
        this.attackDamage = 0.1;
        scene.add.existing(this);
        this.summon = false;
        // this.anims.stop();
        this.setTint(0X82004B);
        this.summoneffect = scene.add.sprite(x, y, CST.SPRITE.SUMMONPLAYERMINION).play(CST.ANIMEFECTS.ENEMYBOSSSUMMON).setDepth(-1);
        this.collision = scene.physics.add.image(x, y, CST.IMAGE.COLLISION).setAlpha(0.01);
        // ボスのＨＰはゴーレムの総量
        this.hp = 30;
        this.enemyHpBar = scene.add.graphics({
            fillStyle: {
                color: 0xFF0000
            }
        }).fillRect(-15, -20, this.hp, 2);
    };
};

export class parameter {
    Choice!: boolean;
    constructor() {
        this.Choice = false;
    }
}
export class witchClass extends Phaser.Physics.Arcade.Sprite {
    // ウィッチの出現場所
    movePoint!: number[][];
    // 召喚場所
    summonPoint!: number[][];
    summonBossPoint!: number[][];
    // 時間によって移動させる
    move!: boolean;
    // 移動中
    duringtTheMove!: boolean;
    // ライフ
    hp!: number;
    enemyHpBar!: Phaser.GameObjects.Graphics;
    // ウィッチの向きアニメーション用
    direction!: string;
    // ウィッチが倒されたアニメーション用
    destroyAnimation!: boolean;
    constructor(scene: Phaser.Scene, x: number, y: number, texture: string) {
        super(scene, x, y, texture)
        scene.add.existing(this);
        scene.physics.world.enable(this);
        this.movePoint = [[384, 32], [64, 320], [704, 192], [160, 128], [608, 64]];
        this.play(CST.ANIMEWITCH.FRONT);
        this.summonPoint = [[160, 32], [256, 64], [96, 256], [128, 416], [448, 480], [640, 352], [640, 192], [640, 96], [544, 32], [96, 160]]
        this.summonBossPoint = [[96, 288], [736, 288]];
        this.hp = 30;
        this.enemyHpBar = scene.add.graphics({
            fillStyle: {
                color: 0xFF0000
            }
        }).fillRect(-15, -30, this.hp, 2);
        this.direction = "front"
        this.destroyAnimation = false;
    }
}