/** @type {import("../typings/phaser")}*/
// CSTをインポート
import { CST } from "../CST";
// import { MenuScene } from "./MenuScene";
// extendsは親クラスを継承した子クラスです
// class クラス名 extends 親クラス名
export class loadScene extends Phaser.Scene {
  constructor() {
    super({
      key: CST.SCENES.LOAD
    });
  }
  LoadImages() {
    this.load.setPath("./gameassets/image");
    for (let prop in CST.IMAGE) {
      // @ts-ignore
      this.load.image(CST.IMAGE[prop], CST.IMAGE[prop]);
    };
  }
  LoadAudio() {
    this.load.setPath("./gameassets/audio");
    for (let prop in CST.AUDIO) {
      // @ts-ignore
      this.load.audio(CST.AUDIO[prop], CST.AUDIO[prop]);
    };
  }
  LoadSprite() {
    this.load.setPath("./gameassets/sprite");
    for (let prop in CST.SPRITE) {
      // @ts-ignore
      this.load.atlas(CST.SPRITE[prop], CST.SPRITE[prop] + ".png", CST.SPRITE[prop] + ".json")
      // @ts-ignore
      this.textures.addSpriteSheetFromAtlas(CST.SPRITE[prop], { frameHeight: CST.SPRITESIZE[prop], frameWidth: CST.SPRITESIZE[prop], atlas: CST.SPRITE[prop], frame: "" });
    };
  }
  preload() {
    // マップの読み込み
    this.load.image("mapPng", "./gameassets/maps/maptexture.png");
    this.load.tilemapTiledJSON("mapStage1", "./gameassets/maps/mapStage1.json");
    this.LoadSprite();
    this.LoadImages();
  };
  create() {
    //アニメーションファイルに移動
    this.scene.start(CST.SCENES.ANIM);
  };
}
